@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: left;
  font-family: "Noto Sans";
}

.mainMail {
  margin-left: 232px;
  margin-right: 232px;
}

.navbar {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #e5e5e5;
}

.navbar .logo {
  position: absolute;
  width: 180px;
  height: 51px;
  left: 20px;
  top: 15px;
}

.mailCards {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.cards {
  width: 70vw;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
}

/* cards styling */

.cardItem {
  width: 300px;
  height: 349px;
  margin: 20px;
  border: 1px solid #dee2e6;
  border-radius: 7px;
  box-sizing: border-box;
}

.cardItem .img {
  height: 126px;
  width: 256px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 1px solid #dee2e6;
}

.cardItem .text {
  margin-left: 8px;
  margin-left: 9px;
}

.cardItem .from {
  font-weight: bold;
}

.cardItem img {
  height: 20px;
  width: 20px;
  float: left;
  margin-right: 5px;
}

.propertyRow {
  font-family: Noto Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 18px;
  color: #606060;
}

.propertyText {
  padding-top: 1px;
}

.mailCount {
  margin: 39px 232px 39px 232px;
  font-family: Noto Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
  display: flex;
  align-items: center;
  color: #606060;
  display: inline-block;
}

.navButtons {
  display: inline-block;
  text-align: right;
}

.previousButton {
  margin-left: 440px;
  font-family: Noto Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-align: center;
  width: 100px;
  height: 38px;
  left: 0px;
  top: 0px;
  background: #ffffff;
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  border-radius: 5px;
  color: #c0c0c0;
  display: inline-block;
}

.nextButton {
  margin-left: 10px;
  font-family: Noto Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-align: center;
  width: 100px;
  height: 38px;
  left: 0px;
  top: 0px;
  background: #ffffff;
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  border-radius: 5px;
  color: #606060;
  display: inline-block;
}

